.ti-Block .ti-ActionList {
  margin-top: 0;
  list-style: none;
}
.ti-Block .ti-ActionInput {
  width: 100%;
}
.ti-Block .ti-ActionInput-container {
  display: flex;
}
.ti-Block .ti-ActionInput-argument {
  padding-left: 10px;
  flex: 1 1 auto;
}
.ti-Block .ti-ActionInput-argument input {
  width: 100%;
}
.ti-Block .ti-ActionInput-argument, .ti-Block .ti-ActionInput-selector {
  display: flex;
}
.ti-Block .ti-ActionInput button {
  margin-left: 15px;
}
.ti-Block .ti-QueuedAction {
  border-bottom: 1px solid #dadee2;
  border-radius: 3px;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
}
.ti-Block .ti-QueuedAction:first-child {
  padding-top: 0;
}
.ti-Block .ti-QueuedAction:last-child {
  border: 0;
  padding-bottom: 0;
}
.ti-Block .ti-QueuedAction-details {
  flex: 1;
  display: flex;
  list-style: none;
  padding: 0 8px 0 0;
  margin: 0;
}
.ti-Block .ti-QueuedAction-part {
  display: flex;
  align-items: center;
}
.ti-Block .ti-QueuedAction-part + .ti-QueuedAction-part {
  margin-left: 10px;
}
.ti-Block .ti-QueuedAction-part:nth-child(2) {
  font-family: system-ui;
}
.ti-Block .ti-QueuedAction-part:nth-child(3) {
  flex: 0 0 auto;
}
.ti-Block .ti-QueuedAction-mainPart {
  flex: 1 1 auto;
}
.ti-Block .ti-QueuedAction-dragger {
  cursor: pointer;
  padding: 0;
  flex: 0 1 auto;
}
.ti-Block .ti-QueuedAction-dragger svg {
  max-height: 15px;
}
.ti-Block .ti-QueuedAction-dragger path {
  fill: #dadee2;
}
.ti-Block .ti-QueuedAction-dragger:hover path {
  fill: #323538;
}
.ti-Block .ti-QueuedAction-input {
  flex: 0 1 25%;
}
.ti-Block .ti-QueuedAction input, .ti-Block .ti-QueuedAction select {
  width: 100%;
  height: 100%;
}
.ti-Block .ti-QueuedAction button {
  border: 0;
  font-size: 0.6em;
  cursor: pointer;
}
.ti-Block .ti-Heading {
  margin: 0 0 5px;
}
.ti-Block h5.ti-Heading {
  margin-bottom: 10px;
  border-bottom: 3px solid rgba(218, 222, 226, 0.5);
  padding-bottom: 7px;
}
.ti-Block .ti-SectionWrapper + .ti-SectionWrapper {
  margin-top: 25px;
}
.ti-Block input,
.ti-Block select {
  border: 1px solid #dadee2 !important;
  border-radius: 3px !important;
  margin: 0;
}
.ti-Block select {
  display: block;
}
.ti-Block input {
  padding-left: 10px !important;
  padding-right: 10px !important;
}
.ti-Block input:disabled {
  background: #dadee2;
}
.ti-Block button {
  display: flex;
  align-items: center;
}
.ti-Block button svg {
  margin-right: 5px;
}

.ti-GlobalSettings-checkboxControl {
  display: flex;
  align-items: center;
}
.ti-GlobalSettings-checkboxControl span {
  transform: translateY(-3px);
}

.ti-HelperText {
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 13px;
  margin-bottom: 15px;
}
.ti-HelperText a {
  display: inline-block;
}